
import { supabase } from '../lib/supabase';

export interface UsuarioPendiente {
  id: string;
  nombre_completo: string;
  email: string;
  rol: string;
  activo: boolean;
  created_at: string;
}

export interface TiendaDisponible {
  id: string;
  nombre: string;
  codigo: string;
  activo: boolean;
}

export interface AsignarTiendaRequest {
  usuario_id: string;
  tienda_id: string;
}

class UsuariosPendientesService {
  async obtenerUsuariosPendientes(): Promise<UsuarioPendiente[]> {
    try {
      console.log('üîç Obteniendo usuarios pendientes...');
      
      // Paso 1: Obtener usuarios que ya tienen tienda asignada
      const { data: usuariosConTienda, error: errorTiendas } = await supabase
        .from('usuario_tiendas')
        .select('usuario_id')
        .eq('activo', true);

      if (errorTiendas) {
        console.error('‚ùå Error obteniendo usuarios con tienda:', errorTiendas);
        throw errorTiendas;
      }

      const idsConTienda = usuariosConTienda?.map(ut => ut.usuario_id) || [];
      console.log('üìã IDs con tienda asignada:', idsConTienda);

      // Paso 2: Obtener usuarios sin tienda asignada
      let query = supabase
        .from('usuarios')
        .select(`
          id,
          nombre_completo,
          email,
          activo,
          created_at,
          roles(nombre)
        `)
        .eq('activo', true);

      // Si hay usuarios con tienda, excluirlos
      if (idsConTienda.length > 0) {
        const idsString = idsConTienda.map(id => `"${id}"`).join(',');
        query = query.not('id', 'in', `(${idsString})`);
      }

      const { data: usuariosPendientes, error: errorUsuarios } = await query;

      if (errorUsuarios) {
        console.error('‚ùå Error obteniendo usuarios pendientes:', errorUsuarios);
        throw errorUsuarios;
      }

      // Formatear respuesta
      const resultado = usuariosPendientes?.map(usuario => ({
        id: usuario.id,
        nombre_completo: usuario.nombre_completo,
        email: usuario.email,
        rol: usuario.roles?.nombre || 'Sin rol',
        activo: usuario.activo,
        created_at: usuario.created_at
      })) || [];

      console.log('‚úÖ Usuarios pendientes obtenidos:', resultado.length);
      return resultado;

    } catch (error) {
      console.error('‚ùå Error en obtenerUsuariosPendientes:', error);
      throw new Error(`Error cargando usuarios pendientes: ${JSON.stringify(error)}`);
    }
  }

  async obtenerTiendasDisponibles(): Promise<TiendaDisponible[]> {
    try {
      console.log('üè™ Obteniendo tiendas disponibles...');
      
      const { data, error } = await supabase
        .from('tiendas')
        .select('id, nombre, codigo, activo')
        .eq('activo', true)
        .order('nombre');

      if (error) {
        console.error('‚ùå Error obteniendo tiendas:', error);
        throw error;
      }

      console.log('‚úÖ Tiendas disponibles:', data?.length || 0);
      return data || [];

    } catch (error) {
      console.error('‚ùå Error en obtenerTiendasDisponibles:', error);
      throw new Error(`Error cargando tiendas: ${JSON.stringify(error)}`);
    }
  }

  async asignarTienda(request: AsignarTiendaRequest): Promise<void> {
    try {
      console.log('üè™ Asignando tienda:', request);

      // Validar que el usuario existe
      const { data: usuario, error: errorUsuario } = await supabase
        .from('usuarios')
        .select('id, nombre_completo')
        .eq('id', request.usuario_id)
        .eq('activo', true)
        .single();

      if (errorUsuario || !usuario) {
        throw new Error('Usuario no encontrado o inactivo');
      }

      // Validar que la tienda existe y est√° activa
      const { data: tienda, error: errorTienda } = await supabase
        .from('tiendas')
        .select('id, nombre')
        .eq('id', request.tienda_id)
        .eq('activo', true)
        .single();

      if (errorTienda || !tienda) {
        throw new Error('Tienda no encontrada o inactiva');
      }

      console.log('‚úÖ Usuario y tienda v√°lidos:', { 
        usuario: usuario.nombre_completo, 
        tienda: tienda.nombre 
      });

      // Crear registro en usuario_tiendas
      const { error: errorAsignacion } = await supabase
        .from('usuario_tiendas')
        .insert({
          usuario_id: request.usuario_id,
          tienda_id: request.tienda_id,
          activo: true
        });

      if (errorAsignacion) {
        console.error('‚ùå Error creando usuario_tiendas:', errorAsignacion);
        throw errorAsignacion;
      }

      console.log('‚úÖ Registro usuario_tiendas creado');

      // Establecer como tienda actual
      const { error: errorTiendaActual } = await supabase
        .from('usuario_tienda_actual')
        .upsert({
          usuario_id: request.usuario_id,
          tienda_id: request.tienda_id
        });

      if (errorTiendaActual) {
        console.error('‚ùå Error estableciendo tienda actual:', errorTiendaActual);
        
        // Rollback: eliminar registro de usuario_tiendas
        await supabase
          .from('usuario_tiendas')
          .delete()
          .eq('usuario_id', request.usuario_id)
          .eq('tienda_id', request.tienda_id);
        
        throw errorTiendaActual;
      }

      console.log('‚úÖ Tienda asignada exitosamente:', { 
        usuario: usuario.nombre_completo, 
        tienda: tienda.nombre 
      });

    } catch (error) {
      console.error('‚ùå Error en asignarTienda:', error);
      throw new Error(`Error asignando tienda: ${JSON.stringify(error)}`);
    }
  }
}

export const usuariosPendientesService = new UsuariosPendientesService();
